import React,{ Component,Fragment } from 'react';
import { connect } from 'react-redux';
import { actionCreators } from '../home/store';
import Header from '../../common/header/index';
import List from '../../common/components/List';
import {
	HotListWrapper,
	HotListItem,
	HotItemIndex
} from './style';


class HotList extends Component{
	render(){
		const {list}=this.props;
		return(
			<Fragment>				
				<Header />
				<HotListWrapper>
					{
						list.map((item,index)=>{
							return(
								<HotListItem key={index}>
									<HotItemIndex className="left">{index+1}</HotItemIndex>
									<div className="right">
										<List index={index} item={item} />
									</div>
									<div style={{clear:'both'}}></div>	
								</HotListItem>
							)
						})
					}
				</HotListWrapper>
			</Fragment>
		)
	}
	componentDidMount(){
		this.props.getHotListData();
	}
}

const mapState=(state)=>({
	list: state.getIn(['home','articleList'])
});
const mapDispatch=(dispatch)=>({
	getHotListData(){
		dispatch(actionCreators.getHotListInfo())
	}
	
});
export default connect(mapState,mapDispatch)(HotList);