export const handleIconfont=(eid,likeid)=>{
	return(dispatch)=>{
		if(likeid===null){			
			axios.get('http://localhost:8080/addEssayApproval.action?eid='+eid+'&uid='+uid).then((res)=>{
				if(res.data==="success"){
					window.location.reload()
				}else{
					alert("点赞失败！！！");
				}
			}).catch((err)=>{
			})
			
		}else{
			axios.get('http://localhost:8080/cancleEssayApproval.action?eid='+eid+'&uid='+uid).then((res)=>{
				if(res.data==="success"){
					window.location.reload()	
				}else{
					alert("取消点赞失败！！！");
				}
			}).catch((err)=>{
			})
		}		
	}	
};
