export const addArticle=(title,text,date1,tid)=>{
	let data = {
		"uid": uid,
		"etitle": title,
		"econtent": text,
		"edate": date1,
		"tid": tid
	}
	return(dispatch)=>{
		axios.post('http://localhost:8080/addEssay.action',
			Qs.stringify(data)
		).then((res)=>{
			if(res.data==="success"){
				alert("文章发布成功！！！");
				window.location.href="/build/#/homePage/myArticle"
			}else{
				alert("文章发布失败！！！");
			}
		}).catch((err)=>{
		})
	}
}
export const addComment=(eid,content,date)=>{
	let data = {
		"uid": uid,
		"eid": eid,
		"econtent": content,
		"date": date
	}	
	axios.post('http://localhost:8080/addComment.action',
		Qs.stringify(data)
	).then((res)=>{
		if(res.data==="success"){
			alert("评论成功！！！");
			window.location.reload()
		}else{
			alert("评论失败！！！");
		}
	}).catch((err)=>{
		alert("评论失败！！！");
	})	
}
export const addResponse=(pid,content,date)=>{
	let data = {
		"uid": uid,
		"pid": pid,
		"rcontent": content,
		"date": date
	}	
	return(dispatch)=>{
		axios.post('http://localhost:8080/addResponse.action',
			Qs.stringify(data)
		).then((res)=>{
			console.log(res.data);
			if(res.data==="success"){
				alert("发布成功！！！");
				window.location.reload()
			}else{
				alert("发布失败！！！");
			}
		}).catch((err)=>{
			alert("发布失败！！！");
		})
	}	
}
