import * as actionTypes from './actionTypes';
import axios from 'axios';
import Qs from 'qs';


export const loginFail=(msg)=>({
 	type: actionTypes.LOGIN_FAIL,
	msg
});
export const clearMessage=()=>({
 	type: actionTypes.CLEAR_MESSAGE
});
export const register=(uid,password)=>{
	let data = {
	    "uid": uid,
	    "password": password
	}
	let storage = window.localStorage;
	return(dispatch)=>{
		axios.post('http://localhost:8080/adduser.action',
			Qs.stringify(data)
		).then((res)=>{		
			const data=res.data;
			if(data==="/home"){			
				storage.uid=uid;
				alert("注册成功！！！");
				window.location.href="/build/#/home"
			}else{	
				alert("该账号已被注册！！！");						
			}
			
		})
	}
}
export const login=(uid,password)=>{
	let data = {
	    "uid": uid,
	    "password": password
	}
	let storage = window.localStorage;
	return (dispatch)=>{
		axios.post('http://localhost:8080/login.action',
			Qs.stringify(data)
		).then((res)=>{		
			const data=res.data;
			if(data==="/home"){			
				storage.uid=uid;
				window.location.href="/build/#/home"
			}else{				
				dispatch(loginFail("账号或密码错误！！！"));			
			}
			
		}).catch((err)=>{

		})
	}
};
export const isDisabled=(show,msg)=>({
 	type: actionTypes.IS_DISABLED,
	show,
	msg
});
