import React,{ Component } from 'react';
import { connect } from 'react-redux';
import { Route,NavLink,Link } from 'react-router-dom';
import {actionCreators} from '../homePage/store'
import Header from '../../common/header/index';
import 'antd/es/button/style/index.css';
import {
	HomePageWrapper,
	HomePageMiddle,
	HomePageBottom,
	UlWrapper
} from './style';
import {	
	Img
} from '../home/style';

class HomePage extends Component{
	render(){
		return(
			<HomePageWrapper>
				<Header />
				<div className="homePageTop">
					<img alt="" src={this.props.users.get("uimg")} />
					<p style={{marginTop:'10px',marginLeft:'46px'}}>{this.props.users.get("uid")}</p>
				</div>
				<HomePageMiddle>				
					<div>
						<ul>
							<NavLink  								
								activeClassName="active" 
								activeStyle={{fontWeight: '600'}} 
								to='/homePage/myQuestion'
							>
								<li>
									我的提问
								</li>
							</NavLink>
							<NavLink 								 
								activeClassName="active" 
								activeStyle={{fontWeight: '600'}} 
								to='/homePage/myArticle'
							>
								<li>
									我的文章
								</li>
							</NavLink>
							<NavLink  								
								activeClassName="active" 
								activeStyle={{fontWeight: '600'}} 
								to='/homePage/myConcern'
							>
								<li>
									我的关注
								</li>
							</NavLink>
							<NavLink  								
								activeClassName="active" 
								activeStyle={{fontWeight: '600'}} 
								to='/homePage/myFollowers'
							>
								<li>
									我的关注者
								</li>
							</NavLink>
						</ul>
				
					</div>
				</HomePageMiddle>
				<HomePageBottom>
					<div className="left">
						{
		   					this.props.routes.map((route,key)=>{		   		
		   						return <Route key={key} exact path={route.path} component={route.component} />		   		
		   					})
		   				}
	   				</div>
	   				<div className="right">
	   					<p>
	   						<span className="left1">推荐关注</span>							
	   					</p>
	   					<UlWrapper>
							{
								this.props.myRecommendList.map((item,index)=>{
									return(
										<li key={index}>
											<Link to='' className="avatar">
												<Img src={item.get("uimg")} alt=''/>
												<span>{item.get("uid")}</span>
											</Link>
											<button onClick={this.props.addConcren.bind(this,item.get("uid"))} className="follow">
												+关注
											</button>
										</li>										
									)									
								})
							}	   							   						
	   					</UlWrapper>	
	   				</div>
	   				<div style={{clear: 'both'}}></div>
				</HomePageBottom>
				
			</HomePageWrapper>
		)
	}
	UNSAFE_componentWillReceiveProps(){
		this.props.getHomepageInfo();
	}
}

const mapState=(state)=>({
	users: state.getIn(['header','users']),
	myRecommendList: state.getIn(['homePage','myRecommendList'])
});
const mapDispatch=(dispatch)=>({
	getHomepageInfo(){
		dispatch(actionCreators.homepageInfo());
	},
	addConcren(uid){
		dispatch(actionCreators.addConcern1(uid))
	}
});

export default connect(mapState,mapDispatch)(HomePage);