import styled from 'styled-components';

export const RelevantWrapper=styled.div`	
	div{
		padding: 5px 20px;
		height: 40px;
		line-height:40px;
	}
	.icon {	       
       margin-right:15px;
       color: black;
    }
`;
export const Img=styled.img`
	float: left;
	display: inline-block;
	border: 1px solid #ddd
	width: 40px;
	height: 40px;
	margin-right:10px;
	border-radius: 50%;
`;
export const Itemlist=styled.div`
	float: left;
	padding-top: 4px;
	.name{
		font-size: 16px;
		font-weight: 500;
		margin-bottom: 3px;
		display: inline-block;
	}
	.detailed{
		font-size: 13px;
		color: #969696;
	}
`;
