import styled from 'styled-components';

export const HotListWrapper=styled.div`
	width: 740px;
	margin: 0 auto;
	background: #FFFFFF;
	border: 1px solid #f0f0f0;
	border-radius: 3px;
	margin-top: 80px;
`;
export const HotListItem=styled.div`
	.left{
		float: left;
	}
	.right{
		width: 680px;
		float: right;
	}
`;
export const HotItemIndex=styled.div`
	text-align: center;
    width: 55px;
    color: #ff9607;
    line-height: 4;
    font-size: 18px;
    font-weight: 600;
`;