import React,{ Component,Fragment } from 'react';
import {connect} from 'react-redux';
import { Link } from 'react-router-dom';
import HeadPortrait from '../../../common/components/HeadPortrait';
import {actionCreators} from '../store/index'
import {
	ResponseItem,
	ResponseTitle
} from '../../home/components/myCorrelation/style'
class MyArticle extends Component{
	render(){
		return(
			<Fragment>
				<div className="kongxin" style={{left: "33%"}}></div>	
				{					
                    (JSON.stringify(this.props.myArticleList)=== '[]')?
                    <div style={{height: '100px',font:'25px',textAlign:'center',marginTop:'50px'}}>还没有发布过文章~</div>
                    :this.props.myArticleList.map((item,index)=>{
						return(
							
								<ResponseItem key={index} style={{overflow:'hidden'}}>
									<Link							
										to={'/detail/'+item.get('eid')} 
										style={{textDecorationLine:'none'}}
									>	
										<HeadPortrait imgUrl={item.get("uimg")} uid={item.get("uid")} date={item.get("edate")}/>
										<ResponseTitle>{item.get("etitle")}</ResponseTitle>		
										<div dangerouslySetInnerHTML={{__html:item.get('econtent')}}  style={{height:'20px',overflow:'hidden',fontSize:'15px',lineHeight:'24px'}}></div>						
									</Link>
									<svg 
										style={{float: "right",marginTop:'10px'}}
										className="icon" 
										aria-hidden="true" 
										onClick={this.props.deleteEssay.bind(this,item.get("eid"))}
									>
										<use xlinkHref={'#iconshanchu'}></use>
									</svg>
								</ResponseItem>
							
						)
					})
				}			
			</Fragment>
		)
	}
}
const mapState=(state)=>({
	myArticleList: state.getIn(['homePage','myArticleList'])
})
const mapDispatch=(dispatch)=>({
	deleteEssay(eid){
		dispatch(actionCreators.deleteEssay(eid))
	}
})
export default connect(mapState,mapDispatch)(MyArticle);