import axios from 'axios';
import * as actionType from './actionType'

const homePageMsg=(result)=>({
	type: actionType.OTHERPAGE_INFO,
	otherArticleList: result.myArticleList,
	otherRecommendList: result.myRecommendList,
	otherFollowerList: result.myFollowerList,
	otherProblemList: result.myProblemList,
	otherConcernList: result.myConcernList
})

export const otherpageInfo=(uid)=>{
	return(dispatch)=>{
		axios.post('http://localhost:8080/getHomepageInfo.action?uid='+uid).then((res)=>{
			const result=res.data;
			console.log(result);
			dispatch(homePageMsg(result));
		})
	}
};
export const getOtherInfo=(uid)=>{
	return(dispatch)=>{
		axios.post('http://localhost:8080/getuserinfo.action?uid='+uid).then((res)=>{
			const result=res.data;
			console.log(result);
			const action={
				type:actionType.OTHER_INFO,
				users: result
			}
			dispatch(action);
		})
	}
};
export const clickIsresponse=(index)=>({	
	type: actionType.HANDLE_RESPONSE,
	index
});
