import { fromJS } from 'immutable';
import * as actionTypes from './actionTypes';
const defaultStore=fromJS({
	topicList:[],
	articleList:[],
	comment:[],
	laterResponseList:[],
	responseList:[],
	attentionList:[],
	needFixed: false,
	index: 0
});

export default (state=defaultStore,action)=>{
	switch(action.type){
		case actionTypes.CHANGE_HOME_DATA:
			return state.merge({
				topicList: fromJS(action.topicList),
				articleList: fromJS(action.articleList)
			});
		case actionTypes.COMMENT_INFO:
			return state.merge({
				comment: fromJS(action.result)
			});
		case actionTypes.LATERRESPONSE_INFO:
			return state.merge({
				laterResponseList: fromJS(action.laterResponseList)
			});
		case actionTypes.RESPONSE_INFO:
			return state.merge({
				responseList: fromJS(action.responseList)
			});
		case actionTypes.ATTENTION_INFO:
			return state.merge({
				attentionList: fromJS(action.attentionList)
			});
		case actionTypes.HANDLE_COMMENT:
			if(state.getIn(['articleList',action.index,'iscomment'])===true){
				return state.setIn(['articleList',action.index,'iscomment'],false);				
			}else{				
				return state.setIn(['articleList',action.index,'iscomment'],true);
			}	
		case actionTypes.HANDLE_RESPONSE:
			if(state.getIn([action.name,action.index,'isresponse'])===true){
				return state.setIn([action.name,action.index,'isresponse'],false);				
			}else{				
				return state.setIn([action.name,action.index,'isresponse'],true);
			}
		case actionTypes.HANDLE_SHARE:
			if(state.getIn(['articleList',action.index,'isShare'])===true){
				return state.setIn(['articleList',action.index,'isShare'],false);
				
			}else{
				return state.setIn(['articleList',action.index,'isShare'],true);
				
			}
		case actionTypes.HANDLE_FIXED:		
			return state.merge({
				needFixed: fromJS(action.show)
			});
		case actionTypes.HANDLE_TOPIC_CLICK:		
			return state.merge({
				index: fromJS(action.index),
				articleList: fromJS(action.list)
			});
		case actionTypes.HOTLIST_INFO:
			return state.merge({
				articleList: fromJS(action.articleList)
			});
		case actionTypes.GET_DETAIL_DATA:
			return state.merge({
				articleList: fromJS(action.articleList)
			});
		default:
			return state;
	}
}