import React,{ Component,Fragment } from 'react';
import { connect } from 'react-redux';
import { actionCreators } from './store';
import { Route, Redirect } from 'react-router-dom';
import Header from '../../common/header/index';
import Relevant from './components/Relevant';

class Home extends Component{
	render(){
	if(localStorage.getItem("uid")!==null){
			return(
				<Fragment>
					<Header />
					<div className="mainContent">          
						<div className="mainContentLeft mainContentCommon">
							{
								this.props.routes.map((route,key)=>{		   		
									return <Route key={key} exact path={route.path} component={route.component} />		   		
								})
							}
						</div>
						<div id='fixed-menu' className={this.props.needFixed?'mainContentRight mainContentCommon fixed':'mainContentRight mainContentCommon'}>
							<Relevant />
						</div>
					</div>	  
				</Fragment>
			)	
		}else{
			return(
				<Redirect to="/" />
			)		
		}	
									
	}
	componentDidMount(){				
		const fixedTop = document.getElementById('fixed-menu').offsetTop;
	    window.onscroll = () => {
	      let scrollTop = Math.max(document.body.scrollTop, document.documentElement.scrollTop)
	      //控制元素块A随鼠标滚动固定在顶部
	      if (scrollTop >= fixedTop) {
	        this.props.setIsFixed(true)
	      } else if (scrollTop < fixedTop) {
	        this.props.setIsFixed(false)
	      }
	    }
	}
}
const mapStateToProps=(state)=>({
	loginvalue: state.getIn(['login','login']),
	needFixed: state.getIn(['home','needFixed'])
});

const mapStateToDispatch=(dispatch)=>({
	setIsFixed(show){
		dispatch(actionCreators.isFixed(show))
	}
});

export default connect(mapStateToProps,mapStateToDispatch)(Home);