import Home from './pages/home/index';
import Article from './pages/home/components/article/index';
import LaterResponse from './pages/home/components/myCorrelation/LaterResponse';
import Main from './pages/home/components/main';
import Detail from './pages/detail/index';
import Login from './pages/login/index';
import Response from './pages/home/components/myCorrelation/Response';
import Collection from './pages/home/components/myCorrelation/Collection';
import Attention from './pages/home/components/myCorrelation/Attention';
import Answer from './pages/answer/index';
import HomePage from './pages/homePage/HomePage';
import OtherPage from './pages/otherPage/OtherPage';
import Register from './pages/register/index';
import HotList from './pages/hotList/index';
import Search from './pages/search/index';
import MyQuestion from './pages/homePage/components/myQuestion';
import MyArticle from './pages/homePage/components/myArticle';
import MyConcern from './pages/homePage/components/myConcern';
import MyFollowers from './pages/homePage/components/myFollowers';

import OtherQuestion from './pages/otherPage/components/OtherQuestion';
import OtherArticle from './pages/otherPage/components/OtherArticle';
import OtherConcern from './pages/otherPage/components/OtherConcern';
import OtherFollowers from './pages/otherPage/components/OtherFollowers';

let routes =[
	{
		path: "/",
		component: Login,
		exact: true,
	},
	{
		path: "/register",
		component: Register,
		exact: true,
	},
	{
		path: "/detail/:id",
		component: Detail,
		exact: true,
	},
	{
		path: "/homePage",
		component: HomePage,
		routes:[
			{
				path: "/homePage/myQuestion",
				component: MyQuestion,				
			}
			,
			{
				path: "/homePage/myArticle",
				component: MyArticle,				
			},
			{
				path: "/homePage/myConcern",
				component: MyConcern,				
			},
			{
				path: "/homePage/myFollowers",
				component: MyFollowers,				
			}
		]
	},
	{
		path: "/OtherPage/:id",
		component: OtherPage,
		routes:[
			{
				path: "/OtherPage/:id/OtherQuestion",
				component: OtherQuestion,				
			}
			,
			{
				path: "/OtherPage/:id/OtherArticle",
				component: OtherArticle,				
			},
			{
				path: "/OtherPage/:id/OtherConcern",
				component: OtherConcern,				
			},
			{
				path: "/OtherPage/:id/OtherFollowers",
				component: OtherFollowers,				
			}
		]
	},
	{
		path: "/answer",
		component: Answer,
		exact: true,
	},
	{
		path: "/hotList",
		component: HotList,
		exact: true,
	},
	{
		path: "/search/:value",
		component: Search,
		exact: true,
	},
	{
		path: "/home",
		component: Home,		
		routes:[
			{
				path: "/home",
				component: Main,				
			},
			{
				path: "/home/article",
				component: Article,							
			},
			{
				path: "/home/laterResponse",
				component: LaterResponse,								
			},
			{
				path: "/home/response",
				component: Response,							
			},
			{
				path: "/home/collection",
				component: Collection,							
			},
			{
				path: "/home/attention",
				component: Attention,							
			}
		]
	}
];

export default routes;