import * as actionTypes from './actionTypes';
import axios from 'axios';
import QS from 'qs';

const uid=localStorage.getItem("uid");

export const loginOut=()=>{
	return(dispatch)=>{	
		localStorage.removeItem('uid');
		window.location.href="/build"
	}
}

export const getUserInfo=()=>{
	return(dispatch)=>{
		axios.post('http://localhost:8080/getuserinfo.action?uid='+uid).then((res)=>{
			const result=res.data;
			const action={
				type:actionTypes.USER_INFO,
				users: result
			}
			dispatch(action);
		})
	}
}
export const getHeaderItem=(item)=>({
	type: actionTypes.INPUT_CONTENT,
	item
});
export const inputChangeAction=(value)=>({
	type: actionTypes.INPUT_VALUE_ACTION,
	value
});
export const inputFocused=()=>({
	type: actionTypes.INPUT_FOCUSED
});
export const inputBlur=()=>({
	type: actionTypes.INPUT_BLUR
});
export const toggleTopShow=(show)=>({
	type: actionTypes.TOGGLE_SCROLL_TOP,
	show
});
export const mouseShow=(show)=>({
	type: actionTypes.MOUSE_SHOW,
	show
});
export const imgShow=(show)=>({
	type: actionTypes.IMG_SHOW,
	show
});
export const handleImgShow=(show)=>({
	type: actionTypes.HANDLE_IMG_SHOW,
	show
});

export const isDisabled=(show)=>({
	type: actionTypes.IS_DISABLED,
	show
});

export const submit=(problem,date)=>{
	let data={
		"uid": uid,
		"pcontent": problem,
		"pdate": date
	}
	return (dispatch)=>{
		axios.post('http://localhost:8080/addproblem.action',
			QS.stringify(data)
		).then((res)=>{			
			const data=res.data;
			if(data==="success"){
				alert("问题发布成功！！！");
				window.location.href="/build/#/homePage/myQuestion"
			}else{
				alert("问题发布失败！！！");
			}
		})
	}
};