import React,{ Component,Fragment } from 'react';
import {connect} from 'react-redux';
import {actionCreators} from '../store/index';
import Userresponse from '../../answer/Userresponse';
import HeadPortrait from '../../../common/components/HeadPortrait';
import {
	ResponseButton,
	ResponseItem,
	ResponseTitle
} from '../../home/components/myCorrelation/style'
class OtherQuestion extends Component{
	render(){
		return(
			<Fragment>
				<div className="kongxin" style={{left: "10%"}}></div>	
				{					
                    (JSON.stringify(this.props.otherProblemList)=== '[]')?
                    <div style={{height: '100px',font:'25px',textAlign:'center',marginTop:'50px'}}>还没有发布过问题~</div>
                    :this.props.otherProblemList.map((item,index)=>{
						return(
							<ResponseItem key={index}>	
								<HeadPortrait imgUrl={item.get("uimg")} uid={item.get("uid")} date={item.get("pdate")}/>
								<ResponseTitle>{item.get("pcontent")}</ResponseTitle>	
								<ResponseButton style={{float:'none'}} onClick={this.props.clickIsresponse.bind(this,index)}>查看所有回答</ResponseButton>																
								{
									item.get('isresponse')?<Userresponse pid={item.get('pid')}/>:null
								}
							</ResponseItem>	
						)
					})
				}								
			</Fragment>
		)
	}
}
const mapState=(state)=>({
	otherProblemList: state.getIn(['otherPage','otherProblemList'])
})
const mapDispatch=(dispatch)=>({
	clickIsresponse(index){
		dispatch(actionCreators.clickIsresponse(index));
	}
})
export default connect(mapState,mapDispatch)(OtherQuestion);