import React,{ Component } from 'react';
import { Link } from 'react-router-dom'
import {connect} from 'react-redux';
import {actionCreators} from '../store/index'
import Button from 'antd/es/button';
import 'antd/es/button/style/index.css';
import {	
	ListItem,
	Img
} from '../../home/style';
import {
	MyConcernWrapper
} from '../style';

class MyConcern extends Component{
	render(){
		return(
			<MyConcernWrapper>
				<div className="kongxin" style={{left: "56%"}}></div>	
				{					
                    (JSON.stringify(this.props.myConcernList)=== '[]')?
                    <div style={{height: '100px',font:'25px',textAlign:'center',marginTop:'50px'}}>还没有关注用户~</div>
                    :this.props.myConcernList.map((item,index)=>{
						return(
							<ListItem key={index} className="item">		
								<Link 
									target="_blank"
									to={'/OtherPage/'+item.get('uid')+'/OtherQuestion'}
								>											
									<Img src={item.get("uimg")} alt="" />	
									<div style={{marginTop:'10px',float:'left'}}>{item.get("uid")}</div>
								</Link>
								<Button onClick={this.props.cancleConcern.bind(this,item.get("uid"))}>取消关注</Button>								
							</ListItem>	
						)
					})									
				}			
			</MyConcernWrapper>
		)
	}
}

const mapState=(state)=>({
	myConcernList: state.getIn(['homePage','myConcernList'])
})
const mapDispatch=(dispatch)=>({
	cancleConcern(id){
		dispatch(actionCreators.cancleConcern(id));
	}
})
export default connect(mapState,mapDispatch)(MyConcern);