import React,{ Component,Fragment } from 'react';
import { connect } from 'react-redux';
import { Link } from 'react-router-dom';
import { actionCreators } from './store';
import Header from '../../common/header/index';
import HeadPortrait from '../../common/components/HeadPortrait';
import {
	SearchWrapper,
	SearchItem
} from './style';

class Search extends Component{
	render(){
		return(
			<Fragment>
				<Header />
				<SearchWrapper>
					{
						(JSON.stringify(this.props.likeEssayList)=== '[]')&&(JSON.stringify(this.props.likeProblemList)=== '[]')?
						<div style={{height: '100px',fontSize:'20px',textAlign:'center',marginTop:'200px'}}>没有搜索到此内容</div>
						:
						(
							<Fragment>
								{
									this.props.likeEssayList.map((item,index)=>{
										return(
											<Link
												key={index}
												to={'/detail/'+item.get('eid')} 
												style={{textDecorationLine:'none'}}
											>
												<SearchItem>
													<div className="right">
														<HeadPortrait imgUrl={item.get("uimg")} uid={item.get("uid")} date={item.get("edate")}/>
														<div style={{marginTop:'10px'}}>
															{item.get('etitle')}
														</div>													
													</div>	
												</SearchItem>
											</Link>
										)										
									})
								}
								{
									this.props.likeProblemList.map((item,index)=>{
										return(
											<SearchItem key={index}>
												<div className="right">
													<HeadPortrait imgUrl={item.get("uimg")} uid={item.get("uid")} date={item.get("pdate")}/>
													<div style={{marginTop:'10px'}}>
														{item.get('pcontent')}
													</div>						
												</div>	
											</SearchItem>
										)
									})
								}								
							</Fragment>
						)
					}					
				</SearchWrapper>
			</Fragment>
		)
	}
	UNSAFE_componentWillReceiveProps(){
		this.props.getSearchData(this.props.match.params.value);
	}
}

const mapState=(state)=>({
	likeEssayList: state.getIn(['search','likeEssayList']),
	likeProblemList: state.getIn(['search','likeProblemList'])
});
const mapDispatch=(dispatch)=>({
	getSearchData(value){
		dispatch(actionCreators.getSearchInfo(value))
	}
});
export default connect(mapState,mapDispatch)(Search);