import React,{ Component,Fragment } from 'react';
import { connect } from 'react-redux';
import { actionCreators } from '../../store/index';
import * as answeractionCreators from '../../../answer/store/actionCreators';
import AnswerItem from '../../../../common/components/AnswerItem';
import Userresponse from '../../../answer/Userresponse';
import {
	ResponseTitle,
	ResponseItem,
	ResponseButton
} from './style.js';

class Attention extends Component{
	render(){
		return(
			<Fragment>
				<ResponseTitle className="bottomStyle">我关注的问题</ResponseTitle>
				{					
					(JSON.stringify(this.props.list)=== '[]')?
					<div style={{height: '100px',font:'25px',textAlign:'center',marginTop:'50px'}}>还没有关注问题~</div>
					:this.props.list.map((item,index)=>{
						return(
							<ResponseItem key={index}>
								<AnswerItem item={item} />
								<div>
									<ResponseButton onClick={this.props.clickIsresponse.bind(this,index)}>查看所有回答</ResponseButton>									
									<ResponseButton onClick={()=>this.props.changeAttention(item.get('pid'),item.get('attentionid'))}>取消关注</ResponseButton>									
								</div>
								{
									item.get('isresponse')?<Userresponse pid={item.get('pid')}/>:null
								}
							</ResponseItem>
						)
					})
				}
			</Fragment>
		)
	}
	componentDidMount(){
		this.props.getAttentionData();
	}
}

const mapState=(state)=>({
	list: state.getIn(['home','attentionList'])
});
const mapDispatch=(dispatch)=>({
	getAttentionData(){
		dispatch(actionCreators.getAttentionInfo())
	},
	changeAttention(pid,attentionid){
		dispatch(answeractionCreators.cancleAttention(pid,attentionid))
	},
	clickIsresponse(index){
		dispatch(actionCreators.clickIsresponse(index,'attentionList'));
	}
});
export default connect(mapState,mapDispatch)(Attention);