import styled from 'styled-components';

export const ArticleWrapper=styled.div`
	position: relative;
	z-index:100;
	padding: 0 20px;
	.WriteIndex-titleInput {
	    margin: 16px 0;
	    border: 0;
	    padding: 10px;
	    width: 100%;
	    height: 100px;
	    position: relative;
	}
	.Input-wrapper {  
	    display: flex;   
	    align-items: center; 
	    font-size: 14px;
	    background: #cfcfcf; 
	    border-radius: 3px;   
	    box-sizing: border-box;
	    transition: background .2s,border .2s;
	}
	.Input {   
	    min-height: 44px;
	    display: block;
	    width: 100%;
	    border: 0;
	    font-size: 32px;
	    line-height: 1.4;
	    font-weight: 600;   
	    outline: none;  
	    box-shadow: none; 
	    flex: 1 1;
	    padding: 0;    
	    font-family: inherit;   
	    background: transparent;
	    resize: none;
	}
	textarea.Input {
	    color: #1a1a1a;
	}
	textarea {
	    text-rendering: auto;    
	    letter-spacing: normal;
	    word-spacing: normal;
	    text-transform: none;
	    text-indent: 0px;
	    text-shadow: none;    
	    text-align: start;
	    -webkit-appearance: textarea;    
	    -webkit-rtl-ordering: logical;
	    flex-direction: column;  
	    cursor: text;
	    white-space: pre-wrap;
	    overflow-wrap: break-word;
	    margin: 0em;
	    font: 400 13.3333px Arial;   
	}
`;
