import axios from 'axios';
import * as actionTypes from './actionTypes';

const uid=localStorage.getItem("uid");

const getAnswerData=(result)=>({
	type: actionTypes.GET_ANSWER_DATA,
	answerList: result
});
export const getAnswerInfo=()=>{
	return(dispatch)=>{
		axios.get('http://localhost:8080/findProblem.action?uid='+uid).then((res)=>{
			const result=res.data;
			const action=getAnswerData(result);
			dispatch(action);
		})
	}
};
export const cancleAttention=(pid,uid)=>{
	return(dispatch)=>{
		axios.get('http://localhost:8080/cancleAttention.action?pid='+pid+'&uid='+uid).then((res)=>{
			const result=res.data;
			if(result==="success"){
				alert("取消关注成功");
				window.location.reload()
			}else{
				alert("取消关注失败");
			}
			
		})
	}
};
export const addAttention=(pid)=>{
	return(dispatch)=>{
		axios.get('http://localhost:8080/addAttention.action?pid='+pid+'&uid='+uid).then((res)=>{
			const result=res.data;
			if(result==="success"){
				window.location.reload()
			}else{
				alert("关注失败");
			}
			
		})
	}
};
export const getAllResponse=(pid)=>{
	return(dispatch)=>{
		axios.get('http://localhost:8080/getAllResponse.action?pid='+pid).then((res)=>{
			const result=res.data;
			const action={
				type:actionTypes.ALLRESPONSE_INFO,
				result
			}
			dispatch(action);
		})
	}
};
export const clickIsresponse=(index)=>({	
	type: actionTypes.HANDLE_RESPONSE,
	index
});
