import styled from 'styled-components';

export const HomePageWrapper=styled.div`
	button{
		float: right;
	}
	margin: 0 auto;
	width: 878.03px;
	margin-top:80px;		
	.homePageTop{
		border: 1px solid #f0f0f0;
		border-radius: 3px;
		background-image: url('http://pic1.win4000.com/wallpaper/d/519ec7f21ceec.jpg');
		background-repeat:no-repeat;
		background-size: 100% 100%;
		height: 170px;
		padding: 0 20px;
		img{
			margin-top: 50px;
			margin-left: 30px;			
			border: 1px solid #ddd;
			height: 60px;
			width: 60px;
			border-radius: 50%;
			background: #FFFFFF;
		}
	}		
`;
export const HomePageMiddle=styled.div`
	border: 1px solid #f0f0f0;
	border-radius: 3px;
	background: #FFF;	
	ul{
		overflow: hidden;
		a{
			margin: 0 30px;
			display: inline-block;
		}
		li{
			width:140px;
			height: 50px;
			line-height: 50px;
			float: left;
			text-align: center;
		}		
		font-size: 16px;
		color: #1a1a1a;
	}
`;
export const HomePageBottom=styled.div`
	background: #fff;
	border: 1px solid #ccc;
	border-radius: 3px;
	margin-top: 10px;
	position: relative;
	padding: 10px 20px;
	.left{
		float: left;
		width: 63%;
		border: 1px solid #ddd;
		border-radius: 5px;
	}
	.right{
		float: right;
		width: 31%;			
		border: 1px solid #ddd
		border-radius: 5px;
		p{
			overflow: hidden;
			padding: 7px 15px;
			font-size: 14px;
    		border-bottom: gray;
    		.left1{
				float: left;
    		}
    		.right1{
    			float: right;
    			.icon{
    				width: 14px;
    				height: 14px;
    			}
    		}
		}
	}
    .kongxin{
        width: 0;
	    height: 0;
	    border: 10px solid transparent;
	    border-bottom-color: black;
	    position: absolute;
	    top: -20px;
    }
`;
export const MyConcernWrapper=styled.div`
	.item{
		margin: 0 20px;
		overflow: hidden;
		.itemlist{
			float: left;
			padding-top: 4px;
			.name{
				font-size: 16px;
    			font-weight: 500;
    			margin-bottom: 3px;
    			display: inline-block;
			}
			.detailed{
				font-size: 13px;
				color: #969696;
			}
		}
		img{
			float: left;
		}
	}
`;
export const UlWrapper=styled.ul`
	margin: 0 10px 20px;
    text-align: left;
    list-style: none;
	padding-left: 10px;
	font-size: 13px;
	color: #333;
	li{
		margin-bottom: 5px;
	}
	img{
		float:left;
	}
	.avatar{
		display: inline-block;
		line-height: 40px;
		span{
	  	float:left;
	  }
	}
	.follow{
		float: right;
    margin-top: 13px;
    margin-right: 20px;
    padding: 0;
    font-size: 13px;
    color: #42c02e;
	}
`;