import React,{ PureComponent } from 'react';
import { Link } from 'react-router-dom';
import { connect } from 'react-redux';
import { actionCreators } from '../store/';
import{
	RelevantWrapper
}from '../../../common/components/style';

class Relevant extends PureComponent{
	render(){
		return(
			<RelevantWrapper>
				<div>
					<Link to="/home/response" >
						<svg className="icon" aria-hidden="true">
						    <use xlinkHref="#iconhuida"></use>
						</svg>
						写回答
					</Link>
				</div>
				<div>
					<Link  to="/home/article" >
						<svg className="icon" aria-hidden="true">
						    <use xlinkHref="#iconxie1"></use>
						</svg>
						写文章
					</Link>
				</div>
				<div>
					<Link to="/home/laterResponse" >
						<svg className="icon" aria-hidden="true">
						    <use xlinkHref="#iconshizhong"></use>
						</svg>
						我的回答
					</Link>
				</div>
				<div>
					<Link to="/home/collection" >
						<svg className="icon" aria-hidden="true">
						    <use xlinkHref="#iconxing-copy-copy"></use>
						</svg>
						我收藏的文章
					</Link>
				</div>
				<div>
					<Link to="/home/attention" >
						<svg className="icon" aria-hidden="true">
						    <use xlinkHref="#iconwenti"></use>
						</svg>
						我关注的问题
					</Link>
				</div>
			</RelevantWrapper>
		)
	}
	componentDidMount(){
		this.props.changeHomeData();
		
	}
}

const mapStateToProps=(state)=>({
	
});

const mapStateToDispatch=(dispatch)=>({
	changeHomeData(){
		const action=actionCreators.getHomeInfo();
		dispatch(action);
	}
});

export default connect(mapStateToProps,mapStateToDispatch)(Relevant);