import React,{ Component } from 'react';
import { connect } from 'react-redux';
import { Route,NavLink } from 'react-router-dom';
import {actionCreators} from '../otherPage/store'
import 'antd/es/button/style/index.css';
import {
	HomePageWrapper,
	HomePageMiddle,
	HomePageBottom
} from './style';

class OtherPage extends Component{
	render(){
		return(
			<HomePageWrapper>
				<div className="homePageTop">
					<img alt="" src={this.props.users.get("uimg")} />
					<p style={{marginTop:'10px',marginLeft:'46px'}}>{this.props.users.get("uid")}</p>
					<p style={{fontSize:'20px',marginTop:'10px',color:'#673ab7'}}>欢迎来到{this.props.users.get("uid")}的主页~</p>
				</div>
				<HomePageMiddle>				
					<div>
						<ul>
							<NavLink  								
								activeClassName="active" 
								activeStyle={{fontWeight: '600'}} 
								to={'/OtherPage/'+this.props.match.params.id+'/OtherQuestion'}
							>
								<li>
									他的提问
								</li>
							</NavLink>
							<NavLink 								 
								activeClassName="active" 
								activeStyle={{fontWeight: '600'}} 
								to={'/OtherPage/'+this.props.match.params.id+'/OtherArticle'}
							>
								<li>
									他的文章
								</li>
							</NavLink>
							<NavLink  								
								activeClassName="active" 
								activeStyle={{fontWeight: '600'}} 
								to={'/OtherPage/'+this.props.match.params.id+'/OtherConcern'}
							>
								<li>
									他的关注
								</li>
							</NavLink>
							<NavLink  								
								activeClassName="active" 
								activeStyle={{fontWeight: '600'}} 
								to={'/OtherPage/'+this.props.match.params.id+'/OtherFollowers'}
							>
								<li>
									他的关注者
								</li>
							</NavLink>
						</ul>
				
					</div>
				</HomePageMiddle>
				<HomePageBottom>
					<div className="left">
						{
		   					this.props.routes.map((route,key)=>{		   		
		   						return <Route key={key} exact path={route.path} component={route.component} />		   		
		   					})
		   				}
	   				</div>
	   				<div style={{clear: 'both'}}></div>
				</HomePageBottom>			
			</HomePageWrapper>
		)
	}
	componentDidMount(){
		console.log(1);
		console.log(this.props);
		this.props.getOtherInfo(this.props.match.params.id);
		this.props.getOtherpageInfo(this.props.match.params.id);
	}
}

const mapState=(state)=>({
	users: state.getIn(['otherPage','users']),
});
const mapDispatch=(dispatch)=>({
	getOtherInfo(uid){
		dispatch(actionCreators.getOtherInfo(uid));
	},
	getOtherpageInfo(uid){
		dispatch(actionCreators.otherpageInfo(uid));
	}
});

export default connect(mapState,mapDispatch)(OtherPage);