import React,{ PureComponent } from 'react';
import { connect } from 'react-redux';
import { Link } from 'react-router-dom';
import { actionCreators } from '../../pages/home/store/';
import Comment from '../../pages/home/components/Comment';
import HeadPortrait from '../../common/components/HeadPortrait';
import {
	ListItem,
	ListInfo,
} from '../../pages/home/style';
import '../../../node_modules/social-share.js/dist/css/share.min.css';
import '../../../node_modules/social-share.js/dist/js/social-share.min.js';

const getShare=(show)=>{
	if(show){
		return(
			<div className="social-share" data-initialized="true">
				<a className="social-share-icon icon-weibo" href="http://service.weibo.com/share/share.php" target="_blank" rel="noopener noreferrer"><span></span></a>新浪微博
				<a className="social-share-icon icon-qzone" href="http://sns.qzone.qq.com/cgi-bin/qzshare/cgi_qzshare_onekey" target="_blank" rel="noopener noreferrer"><span></span></a>QQ空间
				<a className="social-share-icon icon-qq" href="http://connect.qq.com/widget/shareqq/this.props.item.get('id').html?url=https%3A%2F%2Fzzzmh.cn%2Fsingle%3Fid%3D41&amp;" target="_blank" rel="noopener noreferrer"><span></span></a>QQ好友
			</div>
		)
	}else{
		return null
	}
}

class List extends PureComponent{
	render(){
		return(
			<div>														
				<ListItem>
					<Link
					    to={'/detail/'+this.props.item.get('eid')} 
					    style={{textDecorationLine:'none'}}
				    >
						<HeadPortrait imgUrl={this.props.item.get('uimg')} uid={this.props.item.get('uid')} date={this.props.item.get('edate')}/><br/>										
						<h1 className='title'>{this.props.item.get('etitle')}</h1>									
						<ListInfo>											
							<div dangerouslySetInnerHTML={{__html:this.props.item.get('econtent')}} className='desc'></div>
						</ListInfo>
					</Link>
					<div className="listicon">
						<div className="icon1">
							<svg 
								className="icon" 
								aria-hidden="true"
								onClick={this.props.clickIsComment.bind(this,this.props.index)}
							>
							    <use xlinkHref="#iconping-lun-copy"></use>
							</svg>{this.props.item.get('ecomment')}
						</div>
						<div className="icon1"
							onClick={this.props.clickIsShare.bind(this,this.props.index)}
						>
							<svg 
								className="icon" 
								aria-hidden="true"											
							>
							    <use xlinkHref="#iconfenxiang-copy"></use>
							</svg>分享
						</div>
						<div className="icon1">
							<svg 
								className="icon" 
								aria-hidden="true"
								onClick={this.props.clickIsCollect.bind(this,this.props.item.get("eid"),this.props.item.get('collectid'))}
							>
							    <use xlinkHref={this.props.item.get('collectid')?'#iconshoucang':"#iconwuxiaoxing-kong-copy"}></use>
							</svg> {this.props.item.get('ecollect')}
						</div>
						<div className="icon1">
							<svg 
								className="icon" 
								aria-hidden="true" 
								onClick={this.props.handleclick.bind(this,this.props.item.get("eid"),this.props.item.get('likeid'))}
							>
							    <use xlinkHref={this.props.item.get('likeid')?'#iconxinaixin':'#iconganxie-copy'}></use>
							</svg> {this.props.item.get('eapproval')}
						</div>
					</div>
					{getShare(this.props.item.get('isShare'))}
					{
						this.props.item.get('iscomment')?<Comment eid={this.props.item.get('eid')}/>:null
					}
				</ListItem>												
			</div>
			
		)
	}
	
}

const mapState=(state)=>({
	changeIconfont: state.getIn(['home','changeIconfont']),
	comment: state.getIn(['home','comment'])
	
});
const mapDispatch=(dispatch)=>({
	handleclick(eid,likeid){
		dispatch(actionCreators.handleIconfont(eid,likeid));
	},
	clickIsCollect(eid,collectid){
		dispatch(actionCreators.handleIsCollect(eid,collectid));
	},
	clickIsComment(index){
		dispatch(actionCreators.handleIsComment(index));
	},
	clickIsShare(index){
		dispatch(actionCreators.handleIsShare(index));
	}
});


export default connect(mapState,mapDispatch)(List);