import React,{ Component,Fragment } from 'react';
import { connect } from 'react-redux';
import { actionCreators } from '../store';
import Topic from './Topic';
import List from '../../../common/components/List';

class Main extends Component{
	render(){
		return(
			<Fragment>
				<Topic />
				{					
					(JSON.stringify(this.props.list)=== '[]')?
					<div style={{height: '100px',font:'25px',textAlign:'center',marginTop:'50px'}}>没有找到该类型的文章</div>
					:this.props.list.map((item,index)=>{
						return(
							<List key={index} index={index} item={item}/>
						)
					})
				}
			</Fragment>
		)
	}
	componentDidMount(){
		this.props.changeHomeData();
	}
}

const mapState=(state)=>({
	list: state.getIn(['home','articleList']),
});
const mapStateToDispatch=(dispatch)=>({
	changeHomeData(){
		dispatch(actionCreators.getHomeInfo());
	}
});
export default connect(mapState,mapStateToDispatch)(Main);