import React,{ Component } from 'react';
import {connect} from 'react-redux';
import Child from '../../../../common/components/Child';
import {Radio} from 'antd';
import 'antd/es/radio/style/index.css';
import {actionCreators} from '../../store/index';
import {
	ArticleWrapper
} from './style.js';

class Article extends Component{
	render(){
		return(
			<ArticleWrapper>
				<label className="WriteIndex-titleInput Input-wrapper Input-wrapper--multiline">
				<textarea ref={(textarea)=>{this.title=textarea}} rows="1" maxLength='20' required className="Input" placeholder="请输入标题（最多 20 个字）" style={{height: '45px'}}>
				</textarea>
				</label>
				<Child ref="childElem"/>
				<div style={{marginTop: '20px'}}>
					请选择文章所属分类：<br/><br/>					
					<Radio.Group ref="radioElem" defaultValue="1" buttonStyle="solid">
						<Radio.Button style={{margin:'3px'}} value="1">外语</Radio.Button>
						<Radio.Button style={{margin:'3px'}} value="2">计算机</Radio.Button>
						<Radio.Button style={{margin:'3px'}} value="3">理学</Radio.Button>
						<Radio.Button style={{margin:'3px'}} value="4">工学</Radio.Button>
						<Radio.Button style={{margin:'3px'}} value="5">经济管理</Radio.Button>
						<Radio.Button style={{margin:'3px'}} value="6">文史哲</Radio.Button>
						<Radio.Button style={{margin:'3px'}} value="7">艺术设计</Radio.Button>
						<Radio.Button style={{margin:'3px'}} value="8">心理学</Radio.Button>
						<Radio.Button style={{margin:'3px'}} value="9">医药卫生</Radio.Button>
						<Radio.Button style={{margin:'3px'}} value="10">法学</Radio.Button>
						<Radio.Button style={{margin:'3px'}} value="11">教育教学</Radio.Button>
						<Radio.Button style={{margin:'3px'}} value="12">农林园艺</Radio.Button>
						<Radio.Button style={{margin:'3px'}} value="13">其它</Radio.Button>
					</Radio.Group>                        
				</div>				
				<button style={{marginTop: '60px',marginBottom:'20px'}} onClick={()=>this.props.addArticle(this.title,this.refs.childElem,this.refs.radioElem)} className="ReleaseButton">发布</button>
			</ArticleWrapper>				
		)
	}
}
const mapDispatch=(dispatch)=>({
	addArticle(title,data,tid){
		const text=data.state.editorContent;
		if((title.value=== '' ||title.value === null)||(text=== '' ||text === null)){
			alert("请将信息输入完整！！！");
		}else{
			const date=new Date();
			const date1=date.getFullYear() + '-' + (date.getMonth()+1) + '-' + date.getDate();
			dispatch(actionCreators.addArticle(title.value,text,date1,tid.state.value));
		}		
	}
})
export default connect(null,mapDispatch)(Article);