import { fromJS } from 'immutable';
import * as actionTypes from './actionTypes';

const defaultStore=fromJS({
	focused: false,
	showed: false,
	moused: false,
	rightContentShow: true,
	headerShow: true,
	imgFocused: false,
	imgEnter: false,
	headerList: '',
	inputContent: '',
	submitDisabled: true,
	users:[]
});

export default (state=defaultStore,action)=>{
	if(action.type===actionTypes.USER_INFO) {
		return state.merge({
			users: fromJS(action.users)
		})
	}
	if(action.type===actionTypes.INPUT_FOCUSED){
		return state.set('focused',true);
	}
	if(action.type===actionTypes.INPUT_BLUR){
		return state.set('focused',false);
	}
	if(action.type===actionTypes.TOGGLE_SCROLL_TOP){
		return state.set('showed',action.show);
	}
	if(action.type===actionTypes.MOUSE_SHOW){
		return state.set('moused',action.show);
	}
	if(action.type===actionTypes.IMG_SHOW){
		return state.set('imgFocused',action.show);
	}
	if(action.type===actionTypes.HANDLE_IMG_SHOW){
		return state.set('imgEnter',action.show);
	}
	if(action.type===actionTypes.INPUT_CONTENT){
		return state.set('inputContent',action.item);
	}
	if (action.type===actionTypes.INPUT_VALUE_ACTION) {
		return state.set('inputContent',action.value);;
	}
	if (action.type===actionTypes.IS_DISABLED) {
		return state.set('submitDisabled',action.show);;
	}
	return state;
}