import React,{ PureComponent } from 'react';
import { connect } from 'react-redux';
import { actionCreators } from '../store/';
import {
	TopicWrapper,
	TopicItem
}from '.././style';

class Topic extends PureComponent{
	
	render(){
		const {list,handleTopicClick}=this.props;
		return(
			<TopicWrapper>
				<h1>分类</h1>
				{
					list.map((item,index)=>{
						return(
							<TopicItem
							    key={item.get('tid')}
							    className={(index === this.props.index)?'active':''} 
							    onClick={handleTopicClick.bind(this, index,item.get('tid'))}
						    >								
								{item.get('type')}							
							</TopicItem>
						)
					})
				}
			</TopicWrapper>
		)
	}
}

const mapState=(state)=>({
	list: state.getIn(['home','topicList']),
	index: state.getIn(['home','index'])
});
const mapDispatch=(dispatch)=>({
	handleTopicClick(index,id){
		dispatch(actionCreators.handleTopicClick(index,id))
	}
});
export default connect(mapState,mapDispatch)(Topic);