import styled from 'styled-components';

export const ListItem=styled.div`
	position: relative;
	padding: 20px 20px 10px 20px;
	border-bottom: 1px solid #f0f2f7;
	.title{
		line-height: 27px;
		font-size: 18px;
		font-weight: bold;
		color: #333;
		margin-bottom: 10px;
	}
	.listicon{
		overflow:hidden;
		margin-top: 10px;
		.icon1{
			margin-right: 30px;
			float:right;
			cursor: pointer;
			font-size:13px;
			color: #8590a6;
		}
	}
	.social-share{
			padding: 10px 0px;
			text-align: center;
			position: absolute;
			width: 134px;
			height:128px;
			bottom: -148px;
			right: 30px;
			font-size: 14px;			
			background: #fff;
		    border: 1px solid #ebebeb;
		    border-radius: 4px;
			box-shadow: 0 5px 20px rgba(26,26,26,.1);
		    z-index: 100;	
	}
`;
export const Img=styled.img`
	float: left;
	display: inline-block;
	border: 1px solid #ddd
	width: 40px;
	height: 40px;
	margin-right:10px;
	border-radius: 50%;
`;
export const ListInfo=styled.div`
	overflow: hidden;	
	.desc{
		line-height: 24px;
		font-size: 15px;
		color: #1a1a1a;
		height: 50px;
	}
`
export const TopicWrapper=styled.div`
	overflow: hidden;
	padding: 10px 0 10px 10px;
	margin-bottom: 10px;
	border-bottom: 1px solid #f0f2f7;
	h1{
		height: 40px;
		line-height: 40px;
		font: 30px bold;		
		margin-left: 18px;
		margin-bottom: 10px;		
	}
`;
export const TopicItem=styled.div`
	cursor: pointer;
	float: left;
	height: 32px;
	line-height: 32px;
	margin-left: 18px;
	margin-bottom: 10px;
	padding: 0 10px;
	background: #fff;
	font-size: 14px;
	color: #000;
	border: 1px solid #dcdcdc;
	border-radius: 4px;
	.topic-pic{
		display: block;
		float: left;
		width: 32px;
		height: 32px;
		margin-right: 10px;
	}
	&.active{
		background: #ffd53b7a;
		border: 1px solid #793e24;
	}
`;
export const CommentWrapper=styled.div`
	border: 1px solid #f0f2f7;
	border-radius: 4px;
	box-shadow: 0 1px 3px rgba(26,26,26,.1);
    background: #fff;
	margin-top: 10px;
	padding: 15px;
	h1{
		height: 40px;
		line-height: 40px;
		display: block;
		border-bottom: 1px solid #e3e3e3;
	}
	.commentItem{
		border-bottom: 1px solid #e3e3e3;
		padding: 20px 10px;
		img{
			  width: 40px;
		    height: 40px;
		    border: 1px solid #eee;
		    border-radius: 50%;
		    overflow: hidden;
		}
		p{
			margin-top: 10px;
		}
	}
`;