import { combineReducers } from 'redux-immutable';
import { reducer as headerReducer } from '../common/header/store/index';
import { reducer as homeReducer } from '../pages/home/store/index';
import { reducer as homePageReducer} from '../pages/homePage/store/index';
import { reducer as otherPageReducer} from '../pages/otherPage/store/index';
import { reducer as loginReducer} from '../pages/login/store/index';
import { reducer as answerReducer } from '../pages/answer/store/index';
import { reducer as searchReducer } from '../pages/search/store/index';

const reducer=combineReducers({
	header: headerReducer,
	home: homeReducer,
	homePage: homePageReducer,
	otherPage: otherPageReducer,
	login: loginReducer,
	answer: answerReducer,
	search: searchReducer
});

export default reducer;
