import React from 'react';
import {
	Img,
	Itemlist
} from './style';
const HeadPortrait=(props)=>(
	<div style={{display: 'inline-block'}}>
		<Img alt='' src={props.imgUrl} />
		<Itemlist>
			<span className="name">{props.uid}</span><br />
			<span className="detailed">发布于 {props.date}</span>
		</Itemlist>
	</div>
);
export default HeadPortrait;