import React,{ Component } from 'react';
import { Link } from 'react-router-dom';
import { connect } from 'react-redux';
import { actionCreaters } from '../login/store';
import { 
	RegisterWrapper,
	RegisterBox,
	Input,
	Button 
} from './style.js';

class Register extends Component{
	render(){
		return (
			<RegisterWrapper> 
				<RegisterBox>
					<div className="normal-title">
						<Link className="login" to="/">登录</Link>
						<Link className="register" to="/register">注册</Link>
					</div>										
					<Input 
						placeholder='帐号'
						ref={(input)=>{this.account=input}}
						onChange={()=>this.props.checkContent(this.account,this.password)}
						onFocus={this.props.clearMessage}
					/>
					<Input 
						placeholder='密码'
						type='password' 
						ref={(input)=>{this.password=input}}
						onChange={()=>this.props.checkContent(this.account,this.password)}
						onFocus={this.props.clearMessage}
					/>
					<p className="message">{this.props.msg}</p>
					<Button
						disabled={this.props.loginDisabled} 
						onClick={()=>this.props.register(this.account,this.password)} 
						className={this.props.loginDisabled? null : "canClick"}
					>注册</Button>
				</RegisterBox>
		    </RegisterWrapper>
		)
	}
 	
}
const mapState=(state)=>({
	loginDisabled: state.getIn(['login','loginDisabled']),
	msg: state.getIn(['login','msg'])
});
const mapDispatch=(dispatch)=>({
	register(accountElem,passwordElem){
		dispatch(actionCreaters.register(accountElem.value,passwordElem.value))
	},
	checkContent(account,password) {	
	  if((account.value === '' ||account.value === null)||(password.value === '' ||password.value === null)){
			if((account.value === '' ||account.value === null)&&(password.value === '' ||password.value === null)){
				dispatch(actionCreaters.isDisabled(true,'请填写信息！！！'))
			}else if (account.value === '' ||account.value === null) {
				dispatch(actionCreaters.isDisabled(true,'请输入手机号！！！'))
			}else {
				dispatch(actionCreaters.isDisabled(true,'请输入密码！！！'))
			}
			
    }else{
	   	dispatch(actionCreaters.isDisabled(false,''))
    }
  },
  clearMessage(){
  	dispatch(actionCreaters.clearMessage())
  }
});
export default connect(mapState,mapDispatch)(Register);
 
   

