import React,{ Component,Fragment } from 'react';
import { connect } from 'react-redux';
import { actionCreators } from '../../store/index';
import * as answeractionCreators from '../../../answer/store/actionCreators';
import AnswerItem from '../../../../common/components/AnswerItem';
import Userresponse from '../../../answer/Userresponse';
import {
	ResponseTitle,
	ResponseItem,
	ResponseButton,
	ResponseSubHeading
} from './style.js';

class Response extends Component{
	render(){
		return(
			<Fragment>
				<ResponseTitle className="bottomStyle">邀请回答</ResponseTitle>
				<div className="bottomStyle">
					<ResponseSubHeading>
						我关注的人
					</ResponseSubHeading>			
				</div>
				{					
                    (JSON.stringify(this.props.responseList)=== '[]')?
                    <div style={{height: '100px',font:'25px',textAlign:'center',marginTop:'50px'}}>你关注的人里还没有提问过问题~</div>
                    :this.props.responseList.map((item,index)=>{
						return(
							<ResponseItem key={index}>
								<AnswerItem item={item} />
								<div>
									<ResponseButton onClick={this.props.clickIsresponse.bind(this,index)}>查看所有回答</ResponseButton>									
									<ResponseButton onClick={()=>this.props.changeAttention(item.get('pid'),item.get('attentionid'))}>{item.get("attentionid")?'取消关注':'关注问题'}</ResponseButton>									
								</div>
								{
									item.get('isresponse')?<Userresponse pid={item.get('pid')}/>:null
								}
							</ResponseItem>
						)
					})
				}				
				</Fragment>
		)
	}
	componentDidMount(){
		this.props.getResponseData();
	}
}

const mapState=(state)=>({
	responseList: state.getIn(['home','responseList'])
});
const mapDispatch=(dispatch)=>({
	getResponseData(){
		dispatch(actionCreators.getResponseInfo())
	},
	changeAttention(pid,attentionid){
		if(attentionid===null){
			dispatch(answeractionCreators.addAttention(pid));
		}else{
			dispatch(answeractionCreators.cancleAttention(pid,attentionid));
		}
	},
	clickIsresponse(index){
		dispatch(actionCreators.clickIsresponse(index,'responseList'));
	}
});
export default connect(mapState,mapDispatch)(Response);