import styled from 'styled-components';


export const HeaderWrapper=styled.div`
	height: 56px;
	width:100%;	
	position: fixed;	
	left: 0px;
	top: 0px;
	background: #fff;
	z-index:1000;
	box-shadow: 0 1px 3px rgba(26,26,26,.1);
    background-clip: content-box;
`;
export const Nav=styled.div`
	margin: 0 auto;
	height: 56px;
	width: 850px;
`;


export const Logo=styled.img`
	width: 90px;
	margin-top: 10px;
	display: inline-block;	
	margin-left: 20px;
	margin-right: 25px;	
	float: left;
		
`;
export const NavItem=styled.div`
	line-height:52px;
	font: 15px;
	padding: 0 17px;
	&.left{
		float: left;
	}
	&.right{
		img{
			display: block;
			width: 40px;
			height: 40px;
			border-radius: 50%;
			cursor: pointer;
			margin-top: 7px;
			border: 1px solid #ddd
		}
		position: relative;		
		height: 55px;
		float: right;
	}
	&.imgStyle{
		background: #999caa66;
	}
`;
export const ImgWrapper=styled.div`
	position: absolute;	
	top: 56px;
	left: -5px;
	padding: 5px 0px;
	width: 100px;
	background: #fff;
	box-shadow:0 0 8px rgba(0,0,0,.2);
	background: #fff;
	z-index:1000;
	span{
		display: block;
		height: 30px;
		line-height: 30px;
		cursor: pointer;
		padding: 0px 15px;
		&:hover{
			background: #f5f5f5;
		}
	}

`;
export const SearchWrapper=styled.div`
	position: relative;
	float: left;
	.serach{
		position: relative;
		float: left;
		padding: 0px 10px 0px 16px;
		height:32px;
		margin-top: 10px;
		margin-left: 30px;
		background: #f6f6f6;
		border: 1px solid #ebebeb;
		border-radius: 20px;
		.iconfont{
			position:absolute;
			right:0px;
			top:0px;
			display:inline-block;
			cursor: pointer;
			width: 32px;
			line-height: 32px;
			color: #fff;
			background: #999;
			text-align: center;
			border-top-right-radius:4px;
			border-radius:50%;
			padding:0px;
			margin:0px;
		}
	}
	
	
`;
export const SearchInfo=styled.div`
	margin-top:56px;
	margin-left: 30px;
	width:372px;
	padding:0 10px;
	box-shadow:0 0 8px rgba(0,0,0,.2);
	background: #fff;
	position: relative;
	z-index:1000;
`;
export const SearchInfoTitle=styled.div`
	line-height:30px;
	font-size:16px;
	color:#969696;
`;
export const SearchInfoItem=styled.div`
	cursor:pointer;
	line-height:20px;
	padding:6px 0px;
	font-size:14px;
	color:#333;
`;
export const NavSearch=styled.input.attrs({
	placeholder: '搜索'
})`

	width: 298px;
	margin-top:4px;
	transition : all .2s ease-out;
    height: 24px;
	padding: 0;
	border: none;
	outline: none;
	font: 400 14px Arial;
	background: transparent;
	&::placeholder{
		color: #999;
	}
	&.focused{
		width: 372px;
		transition : all .2s ease-out;
	}
`;
export const ButtonType=styled.button`
	float: right;
	margin-top: 10px;
	margin-left: 10px;
	padding: 0 14px;
  color: #fff;
  background-color:#999;
  text-align: center;
  cursor: pointer;
  outline: none;
  border: 1px solid;
  border-radius: 20px;
  font-size: 14px;
  line-height: 33px;
`;
export const Textarea=styled.textarea`
	resize: none;
    width: 450px;
    border: 1px solid #1890ff;
    border-radius: 6px;
    height: 60px;
    font-size: 17px;
    line-height: 30px;
    outline: none;
`;


