import React,{ Component } from 'react';
import Header from '../../common/header/index';
import { connect } from 'react-redux';
import AnswerItem from '../../common/components/AnswerItem';
import { actionCreators } from './store';
import Userresponse from './Userresponse';
import {
	ResponseWrapper,
	ResponseTitle,
	ResponseItem,
	ResponseButton
} from '../home/components/myCorrelation/style';

class Answer extends Component{
	render(){
		return(
			<ResponseWrapper>
				<Header />
				<ResponseTitle className="bottomStyle">等你来答</ResponseTitle>
				{
					this.props.list.map((item,index)=>{
						return(
							<ResponseItem key={index}>
								<AnswerItem item={item} />

								<div>
									<ResponseButton onClick={this.props.clickIsresponse.bind(this,index)}>查看所有回答</ResponseButton>									
									<ResponseButton onClick={()=>this.props.changeAttention(item.get('pid'),item.get('attentionid'))}>{item.get("attentionid")?'取消关注':'关注问题'}</ResponseButton>									
								</div>
								{
									item.get('isresponse')?<Userresponse pid={item.get('pid')}/>:null
								}
							</ResponseItem>
						)
					})
				}
			</ResponseWrapper>
		)
	}
	componentDidMount(){
		this.props.getAnswerData();
	}
}

const mapState=(state)=>({
	list: state.getIn(['answer','answerList'])
});
const mapDispatch=(dispatch)=>({
	getAnswerData(){
		dispatch(actionCreators.getAnswerInfo());
	},
	changeAttention(pid,attentionid){
		if(attentionid===null){
			dispatch(actionCreators.addAttention(pid));
		}else{
			dispatch(actionCreators.cancleAttention(pid,attentionid));
		}
	},
	clickIsresponse(index){
		dispatch(actionCreators.clickIsresponse(index));
	}
});
export default connect(mapState,mapDispatch)(Answer);