import axios from 'axios';
import * as actionType from './actionType'
const uid=localStorage.getItem("uid");

const homePageMsg=(result)=>({
	type: actionType.HOMEPAGE_INFO,
	myArticleList: result.myArticleList,
	myRecommendList: result.myRecommendList,
	myFollowerList: result.myFollowerList,
	myProblemList: result.myProblemList,
	myConcernList: result.myConcernList
})

export const homepageInfo=()=>{
	return(dispatch)=>{
		axios.post('http://localhost:8080/getHomepageInfo.action?uid='+uid).then((res)=>{
			const result=res.data;
			dispatch(homePageMsg(result));
		})
	}
};
export const clickIsresponse=(index)=>({	
	type: actionType.HANDLE_RESPONSE,
	index
});
export const cancleConcern=(id)=>{
	return(dispatch)=>{
		axios.post('http://localhost:8080/deleteConcern.action?uid='+uid+'&rid='+id).then((res)=>{
			console.log(res.data);
			if(res.data==="success"){
				alert("取消关注成功！！！");
				window.location.reload()
			}else{
				alert("取消关注失败！！！");
			}
		})
	}	
}
