import React,{ Component,Fragment } from 'react';
import { connect } from 'react-redux';
import { actionCreators } from '../../store/index';
import HeadPortrait from '../../../../common/components/HeadPortrait';
import {
	ResponseTitle,
	ResponseItem
} from './style.js';

class LaterResponse extends Component{
	render(){
		return(
			<Fragment>
				<ResponseTitle className="bottomStyle">我的回答</ResponseTitle>
				{					
                    (JSON.stringify(this.props.laterResponseList)=== '[]')?
                    <div style={{height: '100px',font:'25px',textAlign:'center',marginTop:'50px'}}>还没有回答过问题~</div>
                    :this.props.laterResponseList.map((item,index)=>{
						return(
							<ResponseItem key={index}>
								<HeadPortrait imgUrl={item.get('uimg')} uid={item.get("uid")} date={item.get("rdate")}/>
								<ResponseTitle>{item.get("rcontent")}</ResponseTitle>
								<div style={{marginLeft:'20px',border:'1px solid #FFEB3B',background:'rgba(158, 158, 158, 0.05)'}}>
									<HeadPortrait imgUrl={item.get('img')} uid={item.get("id")} date={item.get("pdate")}/>
									<ResponseTitle>{item.get("pcontent")}</ResponseTitle>
								</div>
								
							</ResponseItem>
						)
					})
				}
			</Fragment>
		)
	}
	componentDidMount(){
		this.props.getLaterResponseData();
	}
}

const mapState=(state)=>({
	laterResponseList: state.getIn(['home','laterResponseList'])
});
const mapDispatch=(dispatch)=>({
	getLaterResponseData(){
		dispatch(actionCreators.getLaterResponseInfo())
	}
});
export default connect(mapState,mapDispatch)(LaterResponse);