import axios from 'axios';
import * as actionTypes from './actionTypes';

const searchInfo=(result)=>({
	type: actionTypes.SEARCH_INFO,
	likeEssayList: result.likeEssayList,
	likeProblemList: result.likeProblemList
});
export const getSearchInfo=(value)=>{
	return (dispatch)=>{
		axios.get('http://localhost:8080/search.action?content='+value).then((res)=>{
			const result=res.data;
			dispatch(searchInfo(result));
		})
	}
};