import React,{ Component } from 'react';
import { Link } from 'react-router-dom';
import {connect} from 'react-redux';
import Button from 'antd/es/button';
import 'antd/es/button/style/index.css';
import {	
	ListItem,
	Img
} from '../../home/style';
import {
	MyConcernWrapper
} from '../style';
import { actionCreators } from '../store';
class MyFollowers extends Component{
	render(){
		return(
			<MyConcernWrapper>
				<div className="kongxin" style={{left: "79%"}}></div>	
				{					
                    (JSON.stringify(this.props.myFollowerList)=== '[]')?
                    <div style={{height: '100px',font:'25px',textAlign:'center',marginTop:'50px'}}>还没有用户关注你~</div>
                    :this.props.myFollowerList.map((item,index)=>{
						return(
							<ListItem key={index} className="item">		
								<Link to='/'>											
									<Img src={item.get("uimg")} alt="" />	
									<div style={{marginTop:'10px',float:'left'}}>{item.get("uid")}</div>
								</Link>
								<Button onClick={this.props.changeConcern.bind(this,item.get("uid"),item.get("rid"))}>{(item.get("rid")===localStorage.getItem("uid"))?'取消关注':'+关注'}</Button>								
							</ListItem>	
						)
					})
				}								
			</MyConcernWrapper>
		)
	}
}

const mapState=(state)=>({
	myFollowerList: state.getIn(['homePage','myFollowerList'])
})
const mapDispatch=(dispatch)=>({
	changeConcern(uid,rid){
		if(rid===localStorage.getItem("uid")){
			dispatch(actionCreators.cancleConcern(uid));
		}else{
			dispatch(actionCreators.addConcern(uid));
		}
	}
})
export default connect(mapState,mapDispatch)(MyFollowers);