import React,{ Fragment } from 'react';
import HeadPortrait from './HeadPortrait';
import ReplyBox from './ReplyBox'
import {
	ResponseTitle
} from '../../pages/home/components/myCorrelation/style';

const AnswerItem=(props)=>(
	<Fragment>
		<HeadPortrait imgUrl={props.item.get('uimg')} uid={props.item.get("uid")} date={props.item.get("pdate")} />
		<ResponseTitle>{props.item.get('pcontent')}</ResponseTitle>
		写回答：<ReplyBox type="2" id={props.item.get("pid")} />
	</Fragment>
);
export default AnswerItem;