import { fromJS } from 'immutable';
import * as actionTypes from './actionTypes';

const defaultStore=fromJS({
	answerList:[],
	responseList:[]
});

export default (state=defaultStore,action)=>{
	switch(action.type){
		case actionTypes.GET_ANSWER_DATA:
			return state.merge({
				answerList: fromJS(action.answerList)
			});
		case actionTypes.ALLRESPONSE_INFO:
			return state.merge({
				responseList: fromJS(action.result)
			});	
		case actionTypes.HANDLE_RESPONSE:
			if(state.getIn(['answerList',action.index,'isresponse'])===true){
				return state.setIn(['answerList',action.index,'isresponse'],false);				
			}else{				
				return state.setIn(['answerList',action.index,'isresponse'],true);
			}	
		default:
			return state;
	}
}