import React,{ Component } from 'react';
import { connect } from 'react-redux';
import HeadPortrait from '../../common/components/HeadPortrait';
import { actionCreators } from './store/index';
import {
	CommentWrapper
} from '../home/style';

class Userresponse extends Component{
	render(){
		return(
           <CommentWrapper>
                {					
                    (JSON.stringify(this.props.responseList)=== '[]')?
                    <div style={{height: '100px',font:'25px',textAlign:'center',marginTop:'50px'}}>还没有用户回答此问题~</div>
                    :<div>
                        <h1>全部回答</h1>                       
                        {
                            this.props.responseList.map((item,index)=>{
                                return(
                                    <div className="commentItem" key={index}>
                                        <HeadPortrait imgUrl={item.get('uimg')} uid={item.get('uid')} date={item.get('rdate')}/><br/>
                                        <p>{item.get('rcontent')}</p>
                                    </div>
                                )
                            })
                        }
                    </div>                   
                }
            </CommentWrapper>
		)
		
	}
	componentDidMount(){
		this.props.getAllResponse(this.props.pid);
	}
}

const mapState=(state)=>({
	responseList: state.getIn(['answer','responseList'])
});
const mapDispatch=(dispatch)=>({
	getAllResponse(pid){
		dispatch(actionCreators.getAllResponse(pid));
	}
});

export default connect(mapState,mapDispatch)(Userresponse);