import { fromJS } from 'immutable';
import * as actionTypes from './actionTypes';

const defaultStore=fromJS({
	likeEssayList:[],
	likeProblemList:[]
});

export default (state=defaultStore,action)=>{
	switch(action.type){
		case actionTypes.SEARCH_INFO:
			return state.merge({
				likeEssayList: fromJS(action.likeEssayList),
				likeProblemList: fromJS(action.likeProblemList)
			});
		default:
			return state;
	}
}