import { fromJS } from 'immutable';
import * as actionType from './actionType';

const defaultStore=fromJS({
	myArticleList: [],
	myRecommendList: [],
	myFollowerList: [],
	myProblemList: [],
	myConcernList: []
});

export default (state=defaultStore,action)=>{
	switch(action.type){
		case actionType.HOMEPAGE_INFO:
			return state.merge({
				myArticleList: fromJS(action.myArticleList),
                myRecommendList: fromJS(action.myRecommendList),
                myFollowerList: fromJS(action.myFollowerList),
                myProblemList: fromJS(action.myProblemList),
                myConcernList: fromJS(action.myConcernList),				
			});
		case actionType.HANDLE_RESPONSE:
			if(state.getIn(['myProblemList',action.index,'isresponse'])===true){
				return state.setIn(['myProblemList',action.index,'isresponse'],false);				
			}else{				
				return state.setIn(['myProblemList',action.index,'isresponse'],true);
			}	
		default:
		return state;
	}
}