import React,{ Component } from 'react';
import { connect } from 'react-redux';
import ReplyBox from '../../../common/components/ReplyBox';
import HeadPortrait from '../../../common/components/HeadPortrait';
import { actionCreators } from '../store/index';
import {
	CommentWrapper
} from '../style';

class Comment extends Component{
	render(){
		return(
			<CommentWrapper>
				<h1>全部评论</h1>
				<ReplyBox type="1" id={this.props.eid}/>
				
				{
					this.props.comment.map((item,index)=>{
						return(
							<div className="commentItem" key={index}>
								<HeadPortrait imgUrl={item.get('uimg')} uid={item.get('uid')} date={item.get('ecdate')}/><br/>
								<p>{item.get('econtent')}</p>
							</div>
						)
					})
				}
			</CommentWrapper>
		)
		
	}
	componentDidMount(){
		this.props.getCommentInfo(this.props.eid);
	}
}

const mapState=(state)=>({
	comment: state.getIn(['home','comment'])
});
const mapDispatch=(dispatch)=>({
	getCommentInfo(eid){
		dispatch(actionCreators.getCommentInfo(eid));
	}
});

export default connect(mapState,mapDispatch)(Comment);