import styled from 'styled-components';

export const ResponseWrapper=styled.div`
	margin: 0 auto;
	margin-top: 80px;
	width: 800px;
	overflow: hidden;
	background: #FFFFFF;
	border: 1px solid #f0f0f0;
	border-radius: 3px;
`;
export const ResponseTitle=styled.h1`
	font-size: 19px;
	height:auto;	
	color: #1a1a1a;
	padding:10px 20px;
`;
export const ResponseSubHeading=styled.span`			
	display: inline-block;
	margin: 0 20px;
	height: 52px;
	line-height: 52px;
	font-weight: 600;
	border-bottom: 3px solid black;
`;
export const ResponseItem=styled.div`
	padding: 17px 20px 22px;
	border-bottom: 1px solid #f0f0f0;
`;
export const ResponseButton=styled.button`
	color: #8590a6;
    border-color: #8590a6;
    border-radius: 5px;
    padding: 6px;
    font-size: 14px;
    background: none;
    margin-right: 10px;
`;
export const QuestionWrapper=styled.div`
	padding: 30px;
	height: 400px;
	textarea{
		width: 100%;
		height: 200px;
		resize : none;
		margin-bottom: 20px;
	}
`;
