import React,{ Component } from 'react';
import {connect} from 'react-redux';
import {	
	ListItem,
	Img
} from '../../home/style';
import {
	MyConcernWrapper
} from '../style';

class OtherFollowers extends Component{
	render(){
		return(
			<MyConcernWrapper>
				<div className="kongxin" style={{left: "79%"}}></div>	
				{					
                    (JSON.stringify(this.props.otherFollowerList)=== '[]')?
                    <div style={{height: '100px',font:'25px',textAlign:'center',marginTop:'50px'}}>还没有用户关注你~</div>
                    :this.props.otherFollowerList.map((item,index)=>{
						return(
							<ListItem key={index} className="item">												
								<Img src={item.get("uimg")} alt="" />	
								<div style={{marginTop:'10px',float:'left'}}>{item.get("uid")}</div>
							</ListItem>	
						)
					})
				}								
			</MyConcernWrapper>
		)
	}
}

const mapState=(state)=>({
	otherFollowerList: state.getIn(['otherPage','otherFollowerList'])
})
export default connect(mapState,null)(OtherFollowers);