import React,{ Component } from 'react';
import { connect } from 'react-redux';
import Button from 'antd/es/button';
import 'antd/es/button/style/index.css';
import { actionCreators } from '../../pages/home/store/index';

class ReplyBox extends Component{
	render(){
		return(
			<div style={{margin: '10px 0'}}>
				<input ref={(input)=>{this.content=input}} style={{width: '500px',height:'29px',borderRadius: '5px',marginRight:'5px',outline: 'none',border: '1px solid gray'}}/> 
				<Button onClick={this.props.addComment.bind(this,this.props.type,this.props.id)} type="primary">发布</Button>
			</div>
		)
	}
};
const mapState=(state)=>({
});
const mapDispatch=(dispatch)=>({
	addComment(type,id){
		if(this.content.value=== '' ||this.content.value === null){
			alert("请输入你要发布的内容！！！");
		}else{
			const date=new Date();
			const date1=date.getFullYear() + '-' + (date.getMonth()+1) + '-' + date.getDate();
			if(type==="1"){
				dispatch(actionCreators.addComment(id,this.content.value,date1));
			}
			else{
				dispatch(actionCreators.addResponse(id,this.content.value,date1));
			}
			
		}		
	}
});
export default connect(mapState,mapDispatch)(ReplyBox);