import { fromJS } from 'immutable';
import * as actionType from './actionType';

const defaultStore=fromJS({
	users:[],
	otherArticleList: [],
	otherRecommendList: [],
	otherFollowerList: [],
	otherProblemList: [],
	otherConcernList: []
});

export default (state=defaultStore,action)=>{
	switch(action.type){
		case actionType.OTHERPAGE_INFO:
			return state.merge({
				otherArticleList: fromJS(action.otherArticleList),
                otherRecommendList: fromJS(action.otherRecommendList),
                otherFollowerList: fromJS(action.otherFollowerList),
                otherProblemList: fromJS(action.otherProblemList),
                otherConcernList: fromJS(action.otherConcernList),				
			});	
		case actionType.OTHER_INFO:
			return state.merge({
				users: fromJS(action.users)
			});
		case actionType.HANDLE_RESPONSE:
			if(state.getIn(['otherProblemList',action.index,'isresponse'])===true){
				return state.setIn(['otherProblemList',action.index,'isresponse'],false);				
			}else{				
				return state.setIn(['otherProblemList',action.index,'isresponse'],true);
			}
		default:
		return state;
	}
}