import React,{ Component,Fragment } from 'react';
import Modal from 'antd/es/modal';
import Button from 'antd/es/button';
import 'antd/es/modal/style/index.css';
import 'antd/es/button/style/index.css';
import { connect } from 'react-redux';
import  {actionCreators}  from './store';
import { NavLink,Link } from 'react-router-dom';
import BackTop from '../components/BackTop';
import {
	HeaderWrapper,
	Logo,
	Nav,
	NavItem,
	SearchWrapper,
	SearchInfo,
	SearchInfoTitle,
	SearchInfoItem,
	NavSearch,
	ButtonType,
	ImgWrapper,
	Textarea
} from './style';
import imgURL from '../../statics/02.PNG';

class  Header extends Component{
	state = { visible: false };

  showModal = () => {
    this.setState({
      visible: true,
    });
  };

  handleOk = e => {  
    
    this.setState({
      visible: false,
    });
    this.props.submit(this.problem);
  };

  handleCancel = e => {
    this.setState({
      visible: false,
    });
  };
  getAreaList(show){
		if(show){
			return(
				<SearchInfo 
					onMouseEnter={this.props.handleMouse.bind(this,true)}
					onMouseLeave={this.props.handleMouse.bind(this,false)}
				>
					<SearchInfoTitle>
						热门搜索
					</SearchInfoTitle>
					<hr/>
					{
						this.props.articleList.map((item,index)=>{
							return(
								<SearchInfoItem 
									key={index}
									onClick={this.props.getHeaderListItem.bind(this,item.get("etitle"))}
								>
									{item.get("etitle")}
								</SearchInfoItem>
							)
						})
					}
				</SearchInfo>
			)
		}else {
			return null;
		}
	}
	getImg(show){
		if(show){
			return(
				<ImgWrapper
					onMouseEnter={this.props.handleEnter}
					onMouseLeave={this.props.handleLeave}
				>
					<Link to='/homePage/myQuestion'>
						<span>个人主页</span>
					</Link>
					<span onClick={this.props.loginOut}>退出</span>
				</ImgWrapper>
			)
		}else{
			return null;
		}
	}
	render(){
		
		return(
			<Fragment>
				<HeaderWrapper>
					<NavLink 
						to='/home' 
					>
						<Logo src={imgURL} alt='' />
					</NavLink>
					<Nav>
						
						<NavItem className='left'>
							<NavLink 
								to='/home' 
								activeClassName="active" 
								activeStyle={{borderBottom: '3px solid black'}}
								style={{float:'left'}}
							>
								首页
							</NavLink>
						</NavItem>
						<NavItem className='left'>
							<NavLink 
								to='/hotList' 
								activeClassName="active" 
								activeStyle={{borderBottom: '3px solid black'}}
								style={{float:'left'}}
							>
								热榜
							</NavLink>
						</NavItem>
						<NavItem className='left'>
							<NavLink 
								to='/answer' 
								activeClassName="active" 
								activeStyle={{borderBottom: '3px solid black'}}
								style={{float:'left'}}
							>
								等你来答
							</NavLink>
						</NavItem>
						<SearchWrapper>	
							<div className="serach">	
							<NavSearch 
								className={this.props.focused?'focused':''}
								onFocus={this.props.handleInputFocus}
								onBlur={this.props.handleInputBlur}
								value={this.props.inputContent}
								onChange={this.props.handleInputChange}
							>
							</NavSearch>	
							<Link  to={'/search/'+this.props.inputContent}>
								<i className='iconfont'>&#xe60b;</i>
							</Link>	
							</div>
							<ButtonType onClick={this.showModal}>提问</ButtonType>
							{
								this.getAreaList(this.props.focused||this.props.moused)
							}

						</SearchWrapper>
						
						<NavItem className={(this.props.imgFocused||this.props.imgEnter)?'imgStyle right':'right'} 
							onMouseEnter={this.props.handleImgEnter}
							onMouseLeave={this.props.handleImgLeave}
						>
							<img src={this.props.img} alt="" />
							{
								this.getImg(this.props.imgFocused||this.props.imgEnter)
								
							}
						</NavItem>																				
					</Nav>				
				</HeaderWrapper>
				{this.props.showed ? <BackTop/> : null}		
				<Modal
    				title="写下你的问题，准确的描述更容易得到解答"
					footer={
						[
							<Button 
								type={this.props.submitDisabled? null : "primary"} 
								key="test"
								disabled={this.props.submitDisabled} 
											onClick={this.handleOk}
								>
									发布
								</Button>
							]
					}
					visible={this.state.visible}
					onOk={this.handleOk}
					onCancel={this.handleCancel}
					>
						<div>
							<Textarea 
								rows="2" 
								maxLength='30' 
								required  
								placeholder="请描述你的问题（最多 30 个字）"
								ref={(input)=>{this.problem=input}}
								onChange={()=>this.props.isContent(this.problem)}
							>
							</Textarea>
						</div>
        		</Modal>	
			</Fragment>
		)
	}
	componentDidMount(){
		this.props.getUserInfo();
		this.bindEvents();	
	}
	bindEvents(){
		window.addEventListener('scroll',this.props.changeShowScroll)
	}
}

const mapState=(state)=>{
	return{
		img: state.getIn(['header','users','uimg']),
		focused: state.getIn(['header','focused']),
		showed: state.getIn(['header','showed']),
		moused: state.getIn(['header','moused']),
		imgFocused: state.getIn(['header','imgFocused']),
		imgEnter: state.getIn(['header','imgEnter']),
		headerList: state.getIn(['header','headerList']),
		inputContent: state.getIn(['header','inputContent']),
		submitDisabled: state.getIn(['header','submitDisabled']),		
		articleList: state.getIn(['home','articleList'])
	}
}
const mapDispatch=(dispatch)=>{
	return{
		getUserInfo(){
			dispatch(actionCreators.getUserInfo());
		},
		handleInputFocus(){
			dispatch(actionCreators.inputFocused());
		},
		handleInputBlur(){
			dispatch(actionCreators.inputBlur());
		},
		handleMouse(show){
			dispatch(actionCreators.mouseShow(show));
		},
		changeShowScroll(){
			if(document.documentElement.scrollTop>200){
				dispatch(actionCreators.toggleTopShow(true))
			}else{
				dispatch(actionCreators.toggleTopShow(false))
			}
		},
		handleImgEnter(){
			dispatch(actionCreators.imgShow(true));
		},
		handleImgLeave(){
			dispatch(actionCreators.imgShow(false));
		},
		handleEnter(){
			dispatch(actionCreators.handleImgShow(true));
		},
		handleLeave(){
			dispatch(actionCreators.handleImgShow(false));
		},
		loginOut(){
			dispatch(actionCreators.loginOut());
			
		},
		getHeaderListItem(item){
			dispatch(actionCreators.getHeaderItem(item));
		},
		handleInputChange(e){
			dispatch(actionCreators.inputChangeAction(e.target.value));
		},
		submit(problem){
			const date=new Date();
			const date1=date.getFullYear() + '-' + (date.getMonth()+1) + '-' + date.getDate();
			dispatch(actionCreators.submit(problem.value,date1));
		},
		isContent(problem){
			if(problem.value === '' ||problem.value === null){
				dispatch(actionCreators.isDisabled(true));
			}else{
				dispatch(actionCreators.isDisabled(false));
			}
		}
	}
}
export default connect(mapState,mapDispatch)(Header);