import styled from 'styled-components';

export const RegisterWrapper=styled.div`
	background: url('https://s3b.pstatp.com/toutiao/resource/ntoutiao_web/static/image/login/login_bg_7584f6a.png');
	background-repeat:no-repeat;
	background-size:100% 100%; 
	position: absolute;
	top: 0px;
	left: 0px;
	width: 100%;
	height: 100%;
  text-align: center;
  font-size: 14px;   
`;
export const RegisterBox=styled.div`
	border-radius: 5px;
	width: 380px;
	height: 220px;
	margin: 150px auto;
	padding-top: 20px;
	background: #fff;
	box-shadow: 0 0 8px rgba(0,0,0,.1);		
	.normal-title{
		margin-bottom:40px;
		box-sizing: border-box;
		font-weight: 400;
    color: #969696;
    text-align: center;
    font-size: 18px;
    a{		
    	font-weight: 400;
    	color: #969696;
	   	text-decoration: none;	
			padding: 10px;
			cursor: pointer;
		}
		.register{
			font-weight: 700;
		    color: #ea6f5a;
		    border-bottom: 2px solid #ea6f5a;
		    margin-right: 15px;
		}
		.login:hover{
			border-bottom: 2px solid #ea6f5a;
		}
	}
	.message{
		color: red;
	}
	
`;
export const Input=styled.input`
	display: block;
	width: 200px;
	height: 30px;
	line-height: 30px;
	padding: 0 10px;
	margin: 10px auto;
	color: #777;
`;
export const Button=styled.div`
	width: 224px;
	height: 30px;  
	line-height: 30px;
	margin: 0px auto; 
	margin-top: 10px;
	color: #fff;
	background: #ccc;
	border-radius: 5px;
	border: none;
	outline: none;
	text-align: center;
	&.canClick{
		cursor: pointer;
		background: #f85959;
	}
`;