import React,{ Component,Fragment } from 'react';
import { connect } from 'react-redux';
import { actionCreators } from '../../store/index';
import List from '../../../../common/components/List'
import {
	ResponseTitle
} from './style';

class Collection extends Component{
	render(){
		return(
			<Fragment>
				<ResponseTitle className="bottomStyle">我的收藏</ResponseTitle>
				{					
                    (JSON.stringify(this.props.list)=== '[]')?
                    <div style={{height: '100px',font:'25px',textAlign:'center',marginTop:'50px'}}>还没有收藏过文章~</div>
                    :this.props.list.map((item,index)=>{
						return(
							<List key={index} index={index} item={item} />
						)
					})
				}
				
			</Fragment>
		)
	}
	UNSAFE_componentWillReceiveProps(){
		this.props.getCollectionData();
	}
}

const mapState=(state)=>({
	list: state.getIn(['home','articleList'])
});
const mapDispatch=(dispatch)=>({
	getCollectionData(){
		dispatch(actionCreators.getCollectionInfo())
	}
});
export default connect(mapState,mapDispatch)(Collection);